.search-container {
    display: flex;
    justify-content: center;
    margin: 10px;
}

.search-input {
    padding: 8px 15px;
    font-size: 15px;
    width: 350px;
    border-width: 0;
    background-color: #a998f0;
    color: #e7e9f0;
    border-radius: 50px;
    box-shadow: 0 0 5px rgba(255, 255, 255, 0.5);
}

.search-input::placeholder{
    color: #e7e9f0;
    text-align: center;
}

.search-input:focus{
    outline: none;
}